// jest.setup.js - Solution corrigée

// ✅ Mock AsyncStorage - OBLIGATOIRE pour les tests React Native
jest.mock('@react-native-async-storage/async-storage', () =>
  require('@react-native-async-storage/async-storage/jest/async-storage-mock')
);

// ✅ Mock expo-router CORRIGÉ - Sans référence à React dans la factory
jest.mock('expo-router', () => ({
  router: {
    push: jest.fn(),
    replace: jest.fn(),
    back: jest.fn(),
    canGoBack: jest.fn(() => true),
    setParams: jest.fn(),
  },
  useRouter: jest.fn(() => ({
    push: jest.fn(),
    replace: jest.fn(),
    back: jest.fn(),
    canGoBack: jest.fn(() => true),
    setParams: jest.fn(),
  })),
  useLocalSearchParams: jest.fn(() => ({})),
  useGlobalSearchParams: jest.fn(() => ({})),
  useSegments: jest.fn(() => []),
  usePathname: jest.fn(() => '/'),
  // ✅ SOLUTION: useFocusEffect sans référence à React
  useFocusEffect: jest.fn((callback) => {
    // Appeler immédiatement le callback pour les tests
    // Pas besoin d'useEffect ici, c'est un mock
    if (typeof callback === 'function') {
      callback();
    }
  }),
  Redirect: ({ href }) => null,
  Link: ({ href, children, ...props }) => {
    // Utiliser require() à l'intérieur de la fonction, pas dans la factory
    const React = require('react');
    const { TouchableOpacity, Text } = require('react-native');
    return React.createElement(TouchableOpacity, props, 
      typeof children === 'string' 
        ? React.createElement(Text, {}, children)
        : children
    );
  },
  Stack: {
    Screen: ({ children, ...props }) => {
      const React = require('react');
      return React.createElement('div', props, children);
    },
  },
  Tabs: {
    Screen: ({ children, ...props }) => {
      const React = require('react');
      return React.createElement('div', props, children);
    },
  },
}));